.Content {
    margin-top: 128px;
}

.Container {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(4, 1fr);
}

.Article {
    grid-column: 1 / 4;
    grid-row: span 4;
}

.PowerRanks {
    grid-column: 4 / 6;
    grid-row: span 2;
}

.Standings {
    grid-column: 4 / 6;
    grid-row: span 2;
}


