.NavigationItem {
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: -ms-flexbox;
    display: flex;
    height: 100%;
    -ms-flex-align: center;
        align-items: center;
}

.NavigationItem a {
    color: white;
    text-decoration: none;
    height: 100%;
    padding: 16px 10px;
    border-bottom: 4px solid transparent;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: block;
}


.NavigationItem a:hover,
.NavigationItem a:active,
.NavigationItem a.active {
    border-bottom: 4px solid white;
    color: white;
}

@media (min-width: 500px){

    .NavigationItem {
        margin: 0px;
        padding: 14px 10px;
        display: block;
        width: auto;
    }

    .NavigationItem a {
        
    }


    .NavigationItem a:hover,
    .NavigationItem a:active,
    .NavigationItem a.active {
        border-bottom: 4px solid white;
        color: white;
    }
}


